body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #eff0f5;
}

.Header .Sitetitle {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.5rem;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: .02em;
}

@media screen and (min-width: 576px) {
  .Header .Sitetitle {
    margin-bottom: .5rem;
    font-size: 2.5rem;
  }
}

.Header .SiteDescription {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
  line-height: 1;
  opacity: .8;
}

@media screen and (max-width: 576px) {
  .Header .SiteDescription {
    display: none;
  }
}
.Fretboard {
  padding-top: 5rem;
  padding-bottom: 3rem;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 576px) {
  .Fretboard {
    padding-top: 7rem;
    padding-bottom: 4rem;
  }
}

.Fretboard table {
  width: 100%;
  min-width: 44rem;
  border-collapse: collapse;
  table-layout: fixed;
}

.Fretboard td {
  border: 1px solid grey;
}

/* Table header */
.Fretboard th {
  height: 1rem;
}

.Fretboard .Open {
  width: 4rem;
  border: 0;
}

.Fretboard .FretNumbers th:not(.Open) {
  border-right: 1px solid grey;
}

.Fretboard .FretNumbers th.Spacer,
.Fretboard td.Spacer {
  width: 2rem;
  border-right: .25rem solid black;
  border-left: 0;
}

.Fretboard .FretNumber {
  margin-top: -3.5rem;
}

/* Table body */
.Fretboard .Fret {
  position: relative;
  height: 2rem;
  text-align: center;
}

.Fretboard .Fret .Note {
  margin-right: auto;
  margin-left: auto;
  font-size: .875rem;
  border-radius: 50%;
}

.Fretboard .Fret .HalfstepNote {
  display: none;
}

.Fretboard tr:last-of-type td {
  height: 1rem;
  border-bottom: 0;
}

.Fretboard tr:last-of-type .Note {
  margin-top: -1.25rem;
}

/* Notes */
.Fretboard .Note {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  margin-top: -2.25rem;
  font-weight: 600;
  line-height: 1;
}

.Fret .Note-A {
  background-color: #FF8C94;
}

.Fret .Note-B {
  background-color: #DCEDC2;
}

.Fret .Note-C {
  background-color: #FFAAA6;
}

.Fret .Note-D {
  background-color: #A8E6CE;
}

.Fret .Note-E {
  background-color: #FFD3B5;
}

.Fret .Note-F {
  background-color: #b0d2f4;
}

.Fret .Note-G {
  background-color: #c1abea;
}
.TuningButton {
  padding: 1rem 2rem;
  font-size: .875rem;
  font-weight: 500;
  background-color: #eff0f5;
  border: 0;
  border-radius: .125rem;
  cursor: pointer;
}

.TuningButton:hover {
  background-color: #d7dcea;
}

.TuningButton:focus {
  background-color: #d7dcea;
  outline: 0;
}

.TuningButton.Active {
  background-color: #bfc3e2;
}
@media screen and (min-width: 576px) {
  .TuningSelect {
    padding-bottom: 4rem;
  }
}

.TuningSelect fieldset {
  padding: 0;
  margin-right: 0;
  margin-left: 0;
  border: 0;
}

.TuningSelect fieldset legend {
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.TuningSelect .TuningButton {
  margin-right: .5rem;
  margin-bottom: .5rem;
}

@media screen and (max-width: 576px) {
  .TuningSelect .TuningButton {
    width: 100%;
    margin-right: 0;
    margin-bottom: 1rem;
  }
}
.Footer {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  margin-top: auto;
  color: #fff;
  font-weight: 600;
  background-color: #48495d;
}

@media screen and (min-width: 576px) {
  .Footer .Container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.Footer a {
  color: #fff;
}

@media screen and (max-width: 576px) {
  .Footer .Copyright {
    margin-bottom: .25rem;
  }
}
/* Typography */
body {
  color: #333;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Layout */
.Container {
  padding-right: 1rem;
  padding-left: 1rem;
}

@media screen and (min-width: 576px) {
  .Container {
    padding-right: 2rem;
    padding-left: 2rem;
  }
}

.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

